<template>
  <div>
    <!---->
    <el-form :rules="rules" class="login-container" label-position="left" :model="loginForm"
             label-width="0px" v-loading="loading">
      <h3 class="login_title"></h3>
      <el-form-item prop="loginName">
        <el-input type="text" v-model="loginForm.loginName"
                  auto-complete="off" placeholder=""></el-input>
      </el-form-item>
      <el-form-item prop="passWord">
        <el-input type="password" v-model="loginForm.passWord"
                  auto-complete="off" placeholder=""></el-input>
      </el-form-item>
      <el-form-item style="width: 100%">
        <el-button type="primary" style="width: 100%" @click="submitClick"></el-button>
      </el-form-item>
      <el-form-item style="width: 100%">
        <a type="primary" style="float:right;width: 20%;cursor: pointer" @click="forgetPass"></a>
        <a type="primary" style="float:right;width: 20%;cursor: pointer" @click="resign"></a>
      </el-form-item>
    </el-form>

    <!---->
    <el-dialog
      :title="title"
      style="padding: 0px;"
      :visible.sync="dialogVisible"
      width="27%">
      <el-form :model="resignForm" ref="resignForm"  :rules="rules" :inline="true"  style="margin: 0px;padding: 0px; text-align: left">
        <el-form-item prop="picture" style="margin-right: 10px;width: 130px;">
          <el-upload
            class="avatar-uploader"
            action="aaa"
            list-type="picture-card"
            :on-remove="handleImgRemovephoto"
            :before-upload="beforeuploadphoto"
            :show-file-list="false"
          >
            <img v-if="imgurl" :src=imgurl style="width: 120px;" @error="imgurl=false">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label=":" prop="name">
          <el-input prefix-icon="el-icon-edit" v-model="resignForm.name"   placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" prop="loginName">
          <el-input prefix-icon="el-icon-edit" v-model="resignForm.loginName"  placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" prop="passWord">
          <el-input prefix-icon="el-icon-edit" v-model="resignForm.passWord" type="password"  placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" prop="passWord">
          <el-input prefix-icon="el-icon-edit" v-model="resignForm.passWord2" type="password"  placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button  @click="cancelEidt"> </el-button>
        <el-button type="primary" @click="addresignForm('resignForm')"> </el-button>
      </div>
    </el-dialog>

    <!---->
    <el-dialog
      :title="title"
      style="padding: 0px;"
      :visible.sync="dialogVisible1"
      width="27%">
      <el-form :model="resignForm" :rules="rules" ref="restPassFrom" :inline="true"  style="margin: 0px;padding: 0px; text-align: left">
        <el-form-item label=":" prop="loginName">
          <el-input prefix-icon="el-icon-edit" v-model="resignForm.loginName"  placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button  @click="cancelEidt"> </el-button>
        <el-button type="primary" @click="restPass('restPassFrom')"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default{
    data(){  var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        if (this.resignForm.password2 !== '') {
          this.$refs.resignForm.validateField('password2');
        }
        callback();
      }
    };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.resignForm.password) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };
      return {
        imgurl:'',//url
        param: new FormData(),//
        rules: {
          name: [{required: true, message: '', trigger: 'blur'}],
          loginName: [{required: true, message: '', trigger: 'blur'}],
          password: [
            { required: true,validator: validatePass, trigger: 'blur' }
          ],
          password2: [
            { required: true,validator: validatePass2, trigger: 'blur' }
          ],
        },
        checked: true,
        loginForm: {
          loginName: 'admin',
          passWord: '12345'
        },
        loading: false,title:'',
        restForm:{
          loginName:'', //
          passWord:'',
          passWord2:''
        },
        resignForm:{
          name:'', //
          loginName:'', //
          passWord:'', //
          passWord2:'',
          photo:'',
        },
        dialogVisible:false,
        dialogVisible1:false,
      }
    },
    methods: {
      submitClick: function () {
        var _this = this;
        this.loading = true;
        this.postRequest('/user/login', {
          loginName: this.loginForm.loginName,
          passWord: this.loginForm.passWord
        }).then(resp=> {
          _this.loading = false;
          if (resp && resp.status == 200) {
            console.log(resp);
            var data = resp.data;
            if(data.returnValue == "failure"){
              this.$message({type: data.returnValue, message: data.message});
              return;
            }
            if(data!=""){
              _this.$store.commit('login', data.obj);
              var path = _this.$route.query.redirect;
              //
              if(data.obj.roleType == '' ){
                path = path == '/' || path == undefined ? '/home' : path;
                _this.$router.replace({path: path});
              }else{
                path = path == '/' || path == undefined ? '/sys' : path;
                _this.$router.replace({path: path});
              }

            }
          }
        });
      },
      resign: function () {
        this.title = "";
        this.dialogVisible = true;
      },
      addresignForm: function (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            var _this = this;
            if(_this.imgurl == false){
              _this.$alert('', '', {
                confirmButtonText: '',
                callback: action => {
                }
              });
            }
              this.param.append('name', _this.resignForm.name);//
              this.param.append('loginName', _this.resignForm.loginName);//
              this.param.append('passWord', _this.resignForm.passWord);//
            this.uploadFileRequest('/user/resign', this.param).then(resp=> {
              if (resp && resp.status == 200) {
                 var data = resp.data;
                console.log(data);
                this.$message({type: data.returnValue, message: data.message});
                this.cancelEidt();
              }
            });
          }
        })

      },
      cancelEidt() {
        this.dialogVisible = false;
        this.dialogVisible1 = false;

        this.param = new FormData();//
        this.resignForm = {
          name:'', //
          loginName:'', //
          passWord:'', //
          photo:'',
        };
      },
      forgetPass:function(){
        console.log(123);
        this.title = "";
        this.dialogVisible1 = true;
      },
      restPass:function(formName){
        console.log(123);

        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$confirm('{12345}, ?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
              this.doPass();
            }).catch(() => {
            });
          }
          })
      },
      doPass(){
        console.log(this.resignForm.loginName);
        this.postRequest('/user/resetPassWord', {
          loginName: this.resignForm.loginName
        }).then(resp=> {
          //_this.loading = false;
          if (resp && resp.status == 200) {
            console.log(resp);
            var data = resp.data;
            this.$message({type: data.returnValue, message: data.message});
            this.cancelEidt();
          }
        });
      },

      /**/
      //
      handleImgRemovephoto: function (file, fileList) {
        if(file.status == 'ready') return;//
        console.log(file, fileList);
        console.log("");
        this.imgurl = '';
      },
      //upload
      beforeuploadphoto(file) {
        const isJPG = file.type === 'image/png' || file.type === 'image/gif' || file.type === 'image/jpg' || file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 < 5000;

        if (!isJPG) {
          this.$message.error(' pnggifjpgjpeg !');
          return false;
        }
        if (!isLt2M) {
          this.$message.error(' 50KB!');
          return false;
        }
        //
        var windowURL = window.URL || window.webkitURL;
        var src = windowURL.createObjectURL(file);
        this.param.append('file', file, file.name);
        //
        this.imgurl = src;
        return false;
      },
    }
  }
</script>
<style>
  .login-container {
    border-radius: 15px;
    background-clip: padding-box;
    margin: 10% auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }
  .login_title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }
  .login_remember {
    margin: 0px 0px 35px 0px;
    text-align: left;
  }
</style>



