<template>
  <div>
    <el-container>
      <el-header style="padding: 0px;display:flex;justify-content:space-between;align-items: center">
        <div style="display: inline">
          <el-input
            placeholder=",..."
            clearable
            style="width: 300px;margin: 0px;padding: 0px;"
            @keyup.enter.native="searchEmp"
            prefix-icon="el-icon-search"
            v-model="loginName">
          </el-input>
          <el-button type="primary" size="mini" style="margin-left: 5px" icon="el-icon-search" @click="searchEmp">
          </el-button>
        </div>
        <div style="margin-left: 5px;margin-right: 20px;display: inline">
          <el-upload
            :show-file-list="false"
            accept="application/vnd.ms-excel"
            action="/user/import"
            :on-success="fileUploadSuccess"
            :on-error="fileUploadError" :disabled="fileUploadBtnText==''"
            :before-upload="beforeFileUpload" style="display: inline">
            <el-button size="mini" type="success" :loading="fileUploadBtnText==''">
              <i class="fa fa-lg fa-level-up" style="margin-right: 5px"></i>{{fileUploadBtnText}}
            </el-button>
          </el-upload>
          <el-button type="success" size="mini" @click="exportEmps">
            <i class="fa fa-lg fa-level-down" style="margin-right: 5px"></i>
          </el-button>
          <el-button type="primary" size="mini" icon="el-icon-plus"
                     @click="showAddEmpView">
            
          </el-button>
        </div>
      </el-header>
      <el-main style="padding-left: 0px;padding-top: 0px">
        <div>
          <el-table
            :data="users"
            v-loading="tableLoading"
            border
            stripe
            @selection-change="handleSelectionChange"
            size="mini"
            style="width: 100%">
            <el-table-column
              type="selection"
              align="left"
              width="30">
            </el-table-column>
            <el-table-column
              align="left"
              fixed
              label="">
              <template slot-scope="scope">
                <img :src="'http://localhost:8089/user/findById?id='+scope.row.id"  style="width: 70px;height: 70px;margin-right: 5px;margin-left: 5px;border-radius: 70px"/>
              </template>
            </el-table-column>
            <el-table-column
              prop="name"
              align="left"
              label="">
            </el-table-column>
            <el-table-column
              prop="loginName"
              align="left"
              label="">
            </el-table-column>
            <el-table-column
              prop="sex"
              align="left"
              label="">
            </el-table-column>
            <el-table-column
              prop="phone"
              align="left"
              label="">
            </el-table-column>
            <el-table-column
              prop="roleType"
              label="">
            </el-table-column>
            <el-table-column
              prop="keepAlive"
              label="">
            </el-table-column>
            <el-table-column
              prop="updateTime"
              align="left"
              width="195"
              label="">
            </el-table-column>
            <el-table-column
              fixed="right"
              label=""
              width="105">
              <template slot-scope="scope">
                <el-button @click="showEditEmpView(scope.row)" style="padding: 3px 4px 3px 4px;margin: 2px"
                           size="mini">
                </el-button>
                <el-button type="danger" style="padding: 3px 4px 3px 4px;margin: 2px" size="mini"
                           @click="deleteEmp(scope.row)">
                </el-button>
              </template>
            </el-table-column>
          </el-table>
          <div style="display: flex;justify-content: space-between;margin: 2px">
            <el-button type="danger" size="mini" v-if="users.length>0" :disabled="multipleSelection.length==0"
                       @click="deleteManyEmps">
            </el-button>
            <el-pagination
              background
              :page-size="10"
              :current-page="currentPage"
              @current-change="currentChange"
              layout="prev, pager, next"
              :total="totalCount">
            </el-pagination>
          </div>
        </div>
      </el-main>
    </el-container>
    <el-form :model="user" :rules="rules" :inline="true" ref="addEmpForm" style="margin: 0px;padding: 0px;width:1%">
      <div style="text-align: left">
        <el-dialog
          :title="dialogTitle"
          style="padding: 0px;"
          :close-on-click-modal="false"
          :visible.sync="dialogVisible"
          width="27%">

              <el-form-item prop="picture" style="margin-right: 10px;width: 130px;">
                <el-upload
                  class="avatar-uploader"
                  action="aaa"
                  list-type="picture-card"
                  :on-remove="handleImgRemovephoto"
                  :before-upload="beforeuploadphoto"
                  :show-file-list="false"
                >
                  <img v-if="imgurl" :src=imgurl style="width: 120px;" @error="imgurl=false">
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>
                <el-form-item label=":" prop="name">
                  <el-input prefix-icon="el-icon-edit" v-model="user.name" size="mini"
                            placeholder=""></el-input>
                </el-form-item>
                <el-form-item label=":" prop="name">
                  <el-input prefix-icon="el-icon-edit" v-model="user.loginName" size="mini"
                            placeholder=""></el-input>
                </el-form-item>
                <el-form-item label=":" prop="sex">
                  <el-radio-group v-model="user.sex">
                    <el-radio label=""></el-radio>
                    <el-radio style="margin-left: 15px" label=""></el-radio>
                  </el-radio-group>
                </el-form-item>
                <el-form-item label=":" prop="phone">
                  <el-input prefix-icon="el-icon-edit" v-model="user.phone" size="mini"
                            placeholder=""></el-input>
                </el-form-item>
                <el-form-item label=":"  prop="name" style="margin-left: -5px;">
                  <el-select v-model="user.roleType" placeholder="">
                    <el-option
                      v-for="item in roleType"
                      :key="item.id"
                      :label="item.name"
                      :value="item.name">
                    </el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label=":" prop="keepAlive">
                  <el-radio-group v-model="user.keepAlive">
                    <el-radio label=""></el-radio>
                    <el-radio style="margin-left: 15px" label=""></el-radio>
                  </el-radio-group>
                </el-form-item>
            <span slot="footer" class="dialog-footer">
              <el-button size="mini" @click="cancelEidt"> </el-button>
              <el-button size="mini" type="primary" @click="addEmp('addEmpForm')"> </el-button>
            </span>
        </el-dialog>
      </div>
    </el-form>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        imgurl:'',//url
        param: new FormData(),//
        //
        criteria: '',
        //
        pagesize: 10,
        users: [],
        loginName: '',
        fileUploadBtnText: '',
        beginDateScope: '',
        dialogTitle: '',
        multipleSelection: [],
        totalCount: -1,//
        currentPage: 1, //
        dialogVisible: false,
        tableLoading: false,
        roleType:[],
        user: {
          name: '',//
          loginName: '',//
          roleType:'',
          keepAlive: '',//
          sex: '',//
          phone:'' //
        },
        rules: {
          name: [{required: true, message: ':', trigger: 'blur'}],
          loginName: [{required: true, message: ':', trigger: 'blur'}],
          roleType: [{required: true, message: ':', trigger: 'blur'}],
          sex: [{required: true, message: '', trigger: 'blur'}],
          keepAlive: [{required: true, message: '', trigger: 'blur'}]
        }
      };
    },
    mounted: function () {
      this.loadData(this.currentPage,this.pagesize);
      this.loadRoleType();
    },
    methods: {
      loadRoleType() {
        var _this = this;
        this.postRequest("/role/findAllList", {}).then(resp => {
          if (resp && resp.status == 200) {
            var data = resp.data;
            _this.roleType = data;
          }
        })
      },
      loadData(pageNum, pageAmount) {
        var _this = this;
        this.tableLoading = true;
        var param  ={loginName:_this.loginName,pageNum:pageNum, pageAmount:pageAmount}
        this.postRequest("/user/findList", param).then(resp => {
          this.tableLoading = false;
          if (resp && resp.status == 200) {
            var data = resp.data;
            _this.users = data.pagedata;
            _this.totalCount = data.total;
          }
        })
      },
      fileUploadSuccess(response, file, fileList) {
        console.log(response);
        if (response) {
          this.$message({type: response.returnValue, message: response.message});
        }
        this.loadData(this.currentPage,this.pagesize);
        this.fileUploadBtnText = '';
      },
      fileUploadError(err, file, fileList) {
        this.$message({type: 'error', message: "!"});
        this.fileUploadBtnText = '';
      },
      beforeFileUpload(file) {
        this.fileUploadBtnText = '';
      },
      exportEmps() {
        window.open("/user/exportEmp", "_parent");
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      deleteManyEmps() {
        this.$confirm('[' + this.multipleSelection.length + '], ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          var ids = '';
          for (var i = 0; i < this.multipleSelection.length; i++) {
            ids += this.multipleSelection[i].id + ",";
          }
          this.doDelete(ids);
        }).catch(() => {
        });
      },
      deleteEmp(row) {
        this.$confirm('[' + row.name + '], ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.doDelete(row.id);
        }).catch(() => {
        });
      },
      doDelete(ids) {
        this.tableLoading = true;
        var _this = this;
        this.deleteRequest("/user/delete/" + ids).then(resp => {
          _this.tableLoading = false;
          if (resp && resp.status == 200) {
            var data = resp.data;
            _this.loadData(this.currentPage,this.pagesize);
          }
        })
      },
      searchEmp() {
        this.loadData(this.currentPage,this.pagesize);
      },
      currentChange(currentChange) {
        this.currentPage = currentChange;
        this.loadData(this.currentPage, this.pagesize);
      },
      addEmp(formName) {
        var _this = this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            console.log( this.param);
            console.log( this.param.get('file'));

            if (this.user.id) {
              //
              this.tableLoading = true;
              this.param.append('id', _this.user.id);//
              this.param.append('name', _this.user.name);//
              this.param.append('loginName', _this.user.loginName);//
              this.param.append('roleType', _this.user.roleType);//
              this.param.append('keepAlive', _this.user.keepAlive);//
              this.param.append('sex', _this.user.sex);//
              this.param.append('phone', _this.user.phone);//
              this.uploadFileRequest("/user/update", this.param).then(resp => {
                _this.tableLoading = false;
                if (resp && resp.status == 200) {
                  var data = resp.data;
                  _this.dialogVisible = false;
                  _this.emptyEmpData();
                  _this.loadData(this.currentPage,this.pagesize);
                }
              })
            } else {
              //
              this.tableLoading = true;
              if(_this.imgurl == false){
                _this.$alert('', '', {
                  confirmButtonText: '',
                  callback: action => {
                  }
                });
              }
              this.param.append('name', _this.user.name);//
              this.param.append('loginName', _this.user.loginName);//
              this.param.append('roleType', _this.user.roleType);//
              this.param.append('keepAlive', _this.user.keepAlive);//
              this.param.append('sex', _this.user.sex);//
              this.param.append('phone', _this.user.phone);//
              this.uploadFileRequest("/user/insert", this.param).then(resp => {
                _this.tableLoading = false;
                if (resp && resp.status == 200) {
                  var data = resp.data;

                  _this.dialogVisible = false;
                  _this.emptyEmpData();
                  _this.loadData(this.currentPage,this.pagesize);
                }
              })
            }
          } else {
            return false;
          }
        });
      },
      cancelEidt() {
        this.dialogVisible = false;
        this.emptyEmpData();
      },
      showEditEmpView(row) {
        console.log(row)
        this.dialogTitle = "";
        this.user = row;
        this.dialogVisible = true;
        console.log(this.user)
      },
      showAddEmpView() {
        this.dialogTitle = "";
        this.dialogVisible = true;
        this.emptyEmpData();
      },
      emptyEmpData() {
        this.param =  new FormData();//
        this.imgurl = "";
        this.user = {
          name: '',//
          loginName: '',//
          roleType:'',
          keepAlive: '',//
        }
      },

      /**/
      //
      handleImgRemovephoto: function (file, fileList) {
        if(file.status == 'ready') return;//
        console.log(file, fileList);
        console.log("");
        this.imgurl = '';
      },
      //upload
      beforeuploadphoto(file) {
        const isJPG = file.type === 'image/png' || file.type === 'image/gif' || file.type === 'image/jpg' || file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 < 5000;

        if (!isJPG) {
          this.$message.error(' pnggifjpgjpeg !');
          return false;
        }
        if (!isLt2M) {
          this.$message.error(' 50KB!');
          return false;
        }
        //
        var windowURL = window.URL || window.webkitURL;
        var src = windowURL.createObjectURL(file);
        this.param.append('file', file, file.name);
        //
        this.imgurl = src;
        return false;
      },
    }
  };
</script>
<style>
  .el-dialog__body {
    padding-top: 0px;
    padding-bottom: 0px;
  }

  .slide-fade-enter-active {
    transition: all .8s ease;
  }

  .slide-fade-leave-active {
    transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }

  .slide-fade-enter, .slide-fade-leave-to {
    transform: translateX(10px);
    opacity: 0;
  }
</style>



