<template>
  <div>
    <el-container>
      <el-header style="padding: 0px;display:flex;justify-content:space-between;align-items: center;margin-top:20px">
        <div style="display: inline">
          <el-form :model="form" :inline="true" ref="addEmpForm" style="padding: 0px;">
            <el-form-item label=":"  prop="name" style="margin-left: -5px;">
              <el-select v-model="form.type" placeholder="">
                <el-option
                  v-for="item in imgType"
                  :key="item.id"
                  :label="item.type"
                  :value="item.type">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label=":"  prop="name" style="margin-left: -5px;">
              <el-input
                placeholder=",..."
                clearable
                style="width: 300px;margin: 0px;padding: 0px;"
                @keyup.enter.native="searchEmp"
                prefix-icon="el-icon-search"
                v-model="form.name">
              </el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" size="mini" style="margin-left: 5px" icon="el-icon-search" @click="searchEmp"></el-button>
            </el-form-item>
          </el-form>
        </div>
        <div style="margin-left: 5px;margin-right: 20px;display: inline">
          <el-button type="primary" size="mini" icon="el-icon-plus"
                     @click="showAddEmpView">
            
          </el-button>
        </div>
      </el-header>
      <el-main style="padding-left: 0px;padding-top: 0px">
        <div>
          <el-table
            :data="tableData"
            v-loading="tableLoading"
            border
            stripe
            @selection-change="handleSelectionChange"
            size="mini"
            style="width: 100%">
            <el-table-column
              type="selection"
              align="left"
              width="30">
            </el-table-column>
            <el-table-column
              align="left"
              label="">
              <template slot-scope="scope">
                <img  :src="scope.row.url+'img/findById?imgid='+scope.row.id" width="200px"/>
              </template>
            </el-table-column>
            <el-table-column
              prop="name"
              align="left"
              label="">
            </el-table-column>
            <el-table-column
              prop="type"
              align="left"
              label="">
            </el-table-column>
            <el-table-column
              fixed="right"
              label=""
              width="195">
              <template slot-scope="scope">
                <el-button @click="showEditEmpView(scope.row)" style="padding: 3px 4px 3px 4px;margin: 2px"
                           size="mini">
                </el-button>
                <el-button slot="reference" type="primary" size="mini" style="padding: 3px 4px 3px 4px;margin: 2px"
                                        @click="downloadImg(scope.row)">
               </el-button>
                <el-button type="danger" style="padding: 3px 4px 3px 4px;margin: 2px" size="mini"
                           @click="deleteEmp(scope.row)">
                </el-button>
              </template>
            </el-table-column>
          </el-table>
          <div style="display: flex;justify-content: space-between;margin: 2px">
            <el-button type="danger" size="mini" v-if="tableData.length>0" :disabled="multipleSelection.length==0"
                       @click="deleteManyEmps">
            </el-button>
            <el-pagination
              background
              :page-size="10"
              :current-page="currentPage"
              @current-change="currentChange"
              layout="prev, pager, next"
              :total="totalCount">
            </el-pagination>
          </div>
        </div>
      </el-main>
    </el-container>
    <el-form :model="ruleForm" :rules="rules" :inline="true" ref="addEmpForm" style="margin: 0px;padding: 0px;width:1%">
      <div style="text-align: left">
        <el-dialog
          :title="dialogTitle"
          style="padding: 0px;"
          :close-on-click-modal="false"
          :visible.sync="dialogVisible"
          width="27%">
            <el-form-item label=":"  prop="name" style="margin-left: -5px;">
              <el-input v-model="ruleForm.name" ></el-input>
            </el-form-item>
            <el-form-item label=":"  prop="name" style="margin-left: -5px;">
              <el-select v-model="ruleForm.type" placeholder="">
                <el-option
                  v-for="item in imgType"
                  :key="item.id"
                  :label="item.type"
                  :value="item.type">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label=":"  prop="url" style="margin-left: -5px;">
              <el-input v-model="ruleForm.url" ></el-input>
            </el-form-item>
            <el-form-item prop="picture" style="margin-right: 10px;width: 130px;">
              <el-upload
                class="avatar-uploader"
                action="aaa"
                list-type="picture-card"
                :on-remove="handleImgRemovephoto"
                :before-upload="beforeuploadphoto"
                :show-file-list="false"
              >
                <img v-if="imgurl" :src=imgurl style="width: 120px;" @error="imgurl=false">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
          <span slot="footer" class="dialog-footer">
              <el-button size="mini" @click="cancelEidt"> </el-button>
              <el-button size="mini" type="primary" @click="addEmp('addEmpForm')"> </el-button>
            </span>
        </el-dialog>
      </div>
    </el-form>
  </div>
</template>
<script>
  import $axios from 'axios'
  export default {
    mounted: function () {
      this.loadData(this.criteria,this.currentPage,this.pagesize);
      this.loadType();
    },
    methods: {
      loadData(criteria,pageNum, pageAmount) {
        var _this = this;
        this.tableLoading = true;
        var param  ={name:_this.form.name,type:_this.form.type,pageNum:pageNum, pageAmount:pageAmount}
        this.postRequest("/img/findList", param).then(resp => {
          this.tableLoading = false;
          if (resp && resp.status == 200) {
            var data = resp.data;
            _this.tableData = data.pagedata;
            _this.totalCount = data.total;
          }
        })
      },
      loadType() {
        var _this = this;
        this.postRequest("/imgType/findAllList", {}).then(resp => {
          if (resp && resp.status == 200) {
            var data = resp.data;
            _this.imgType = data;
          }
        })
      },
      /**/
      //
      handleImgRemovephoto: function (file, fileList) {
        if(file.status == 'ready') return;//
        console.log(file, fileList);
        console.log("");
        this.imgurl = '';
      },
      //upload
      beforeuploadphoto(file) {
        const isJPG = file.type === 'image/png' || file.type === 'image/gif' || file.type === 'image/jpg' || file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 < 5000;

        if (!isJPG) {
          this.$message.error(' pnggifjpgjpeg !');
          return false;
        }
        if (!isLt2M) {
          this.$message.error(' 50KB!');
          return false;
        }
        //
        var windowURL = window.URL || window.webkitURL;
        var src = windowURL.createObjectURL(file);
        this.param.append('file', file, file.name);
        //
        this.imgurl = src;
        return false;
      },

      downloadImg(row){
          var src='data:image/png;base64,'+ row.picture;
          this.srcImg = src; //
          var link = document.createElement('a');
          link.href = src;
          link.download =row.name+ ".jpg";
          link.click();
        },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      deleteManyEmps() {
        this.$confirm('[' + this.multipleSelection.length + '], ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          var ids = '';
          for (var i = 0; i < this.multipleSelection.length; i++) {
            ids += this.multipleSelection[i].id + ",";
          }
          this.doDelete(ids);
        }).catch(() => {
        });
      },
      deleteEmp(row) {
        this.$confirm('[' + row.name + '], ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.doDelete(row.id);
        }).catch(() => {
        });
      },
      doDelete(ids) {
        this.tableLoading = true;
        var _this = this;
        this.deleteRequest("/img/delete/" + ids).then(resp => {
          _this.tableLoading = false;
          if (resp && resp.status == 200) {
            var data = resp.data;
            _this.loadData(this.criteria,this.currentPage,this.pagesize);
          }
        })
      },
      searchEmp() {
        this.loadData(this.criteria,this.currentPage,this.pagesize);
      },
      currentChange(currentChange) {
        this.currentPage = currentChange;
        this.loadData(this.criteria,this.currentPage, this.pagesize);
      },
      addEmp(formName) {
        var _this = this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            if (this.ruleForm.id) {
              //
              this.tableLoading = true;
              this.param.append('id', _this.ruleForm.id);//
              this.param.append('name', _this.ruleForm.name);//
              this.param.append('type', _this.ruleForm.type);//
              this.param.append('url', _this.ruleForm.url);//
              this.uploadFileRequest("/img/update", this.param).then(resp => {
                _this.tableLoading = false;
                if (resp && resp.status == 200) {
                  var data = resp.data;
                  _this.dialogVisible = false;
                  _this.emptyEmpData();
                  _this.loadData(this.criteria,this.currentPage,this.pagesize);
                }
              })
            } else {
              //
              if(_this.imgurl == false){
                _this.$alert('', '', {
                  confirmButtonText: '',
                  callback: action => {
                  }
                });
              }
              this.tableLoading = true;
              this.param.append('name', _this.ruleForm.name);//
              this.param.append('type', _this.ruleForm.type);//
              this.param.append('url', _this.ruleForm.url);//
              this.uploadFileRequest("/img/insert", this.param).then(resp => {
                _this.tableLoading = false;
                if (resp && resp.status == 200) {
                  var data = resp.data;

                  _this.dialogVisible = false;
                  _this.emptyEmpData();
                  _this.loadData(this.criteria,this.currentPage,this.pagesize);
                }
              })
            }
          } else {
            return false;
          }
        });
      },
      cancelEidt() {
        this.dialogVisible = false;
        this.emptyEmpData();
      },
      showEditEmpView(row) {
        console.log(row)
        this.dialogTitle = "";
        this.ruleForm = row;
        this.dialogVisible = true;
      },
      showAddEmpView() {
        this.dialogTitle = "";
        this.dialogVisible = true;
        this.emptyEmpData();
      },
      emptyEmpData() {
        this.imgurl = '';//url
        this.param = new FormData();//
        this.ruleForm = {
          id:'',//
          name:'', //
          picture:'',//
          type:'', //
          url:'http://localhost:8089/'
        }
      }
    },

    data() {
      return {
        form:{
          name:'',
          type:''
        },
        //
        criteria: '',
        //
        pagesize: 10,
        tableData: [],
        picture:'',
        name: '',
        subItem:'',
        fileUploadBtnText: '',
        beginDateScope: '',
        dialogTitle: '',
        multipleSelection: [],
        totalCount: -1,//
        currentPage: 1, //
        dialogVisible: false,
        tableLoading: false,
        imgType:[],//
        imgurl:'',//url
        param: new FormData(),//
        //
        ruleForm:{
          id:'',//
          name:'', //
          picture:'',//
          type:'', //
          url:'http://localhost:8089/'
        },
        rules: {
          name: [{required: true, message: '', trigger: 'blur'}],
          type: [{required: true, message: '', trigger: 'blur'}]
        }
      };
    },
  };
</script>
<style>
  .el-dialog__body {
    padding-top: 0px;
    padding-bottom: 0px;
  }

  .slide-fade-enter-active {
    transition: all .8s ease;
  }

  .slide-fade-leave-active {
    transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }

  .slide-fade-enter, .slide-fade-leave-to {
    transform: translateX(10px);
    opacity: 0;
  }
</style>



