import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Home from '@/components/Home'//
import sys from '@/components/Home2'//
import EmpBasic from '@/components/emp/EmpBasic'//---
import Img from '@/components/emp/Img'//---
import ImgType from '@/components/emp/ImgType'//---
import Role from '@/components/emp/Role'//---
import Oplog from '@/components/emp/Oplog'//---
import Loginlog from '@/components/emp/Loginlog'//---
import CommentBasic from '@/components/emp/CommentBasic'//---
import ImgHost from '@/components/system/imgHost'//---
import UserHome from '@/components/system/userHome'//---
import UserHost from '@/components/system/userHost'//-----
import UserBasic from '@/components/system/UserBasic'//-----
import Comment from '@/components/system/comment'//-----


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login,
      hidden: true
    }, {
      path: '/home',
      name: '',
      component: Home,
      hidden: true,
      meta: {
        requireAuth: true
      },
      children: [
      ]
    }, {
      path: '/UserHost',
      name: '',
      component: UserHome,
      hidden: true,
      meta: {
        requireAuth: true
      },
      children: [
        {
          path: '/UserHost',
          name: '',
          component: UserHost,
          meta: {
            keepAlive: false
          }
        },{
          path: '/comment',
          name: '',
          component: Comment,
          meta: {
            keepAlive: false
          }
        },{
          path: '/UserBasic',
          name: '',
          component: UserBasic,
          meta: {
            keepAlive: false
          }
        },
      ]
    }, {
      path: '/sys',
      name: '',
      component: sys,
      hidden: true,
      meta: {
        requireAuth: true
      }
    },{
      path: '/imghost',
      name: '',
      component: ImgHost,
      hidden: true,
      meta: {
        keepAlive: false
      }
    },
    {
      path: '/home',
      name: '',
      component: Home,
      children: [
        {
          path: '/EmpBasic',
          name: '',
          component: EmpBasic,
          meta: {
            keepAlive: false
          }
        },
        {
          path: '/img',
          name: '',
          component: Img,
          meta: {
            keepAlive: false
          }
        },
        {
          path: '/imgType',
          name: '',
          component: ImgType,
          meta: {
            keepAlive: false
          }
        },
        {
          path: '/role',
          name: '',
          component: Role,
          meta: {
            keepAlive: false
          }
        },
        {
          path: '/log',
          name: '',
          component: Oplog,
          meta: {
            keepAlive: false
          }
        },
        {
          path: '/loginLog',
          name: '',
          component: Loginlog,
          meta: {
            keepAlive: false
          }
        },
        {
          path: '/CommentBasic',
          name: '',
          component: CommentBasic,
          meta: {
            keepAlive: false
          }
        },
      ]
    },

  ]
})



